.mainnav{
    display: flex;
    position: relative;
    
}
#product{
    font-size: 1.4rem;
    display: inline-block;
    text-transform: uppercase;
    font-weight: 700;
    color: #fff;
}
#productdiv{
    padding: 15px;
    margin-left: 12%; 
}
.nav_top{
    padding: 15px;
    font-weight:500;
    color:white;
    padding-left: 25px;
    padding-right: 25px;
    flex-direction: row;
    display: inline-block;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    line-height: 1;

}
.main{
    background-color: #394f62;
}
.nav{
    margin-left: 70px;
}
.icon{
    margin-left: 21px;
    margin-bottom: 0px;
   font-size: 30px;
   margin-top: 10px;
}
.nav_top a{
    text-decoration: none;
    color: white;
}
.nav_top li{
   list-style-type: none;
}
#dashboard{
 
background-color: #f5a623;
position:relative;
}
.nav_top:hover{
    color: #f5a623;
}
.nav_top li:hover{
    color:#f5a623;
}
#login{
    color: #fff;
    padding: 15px;
    flex-direction: row;
    margin-top: 28px;
    margin-left: 100px;
    text-decoration: none;
}
.body{
    background-color: #394f62;
    /* position: fixed; */
}
 .body_first{
    padding-bottom:90%;
    padding-top: 20px;
    color: #fff !important;
    
}
.body_first p{
    margin-top: 40px;
    margin-left: 15%;
}
select{
    box-sizing: content-box;
    text-indent: 15%;
    line-height: 82px;
    border: none;
    background-color:#567086;
    color: #fff;
    
}
select li{
    margin: 30%;
}
option{
    display: block;
    white-space: nowrap;
    min-height: 1.2em;
    padding: 16px;
}

#report option:hover{
color: #f5a623;
}

select:hover{
    color: #f5a623;
}